/*Other CSS*/
body {
  font-family: helvetica;
  font-size: 1.2em;
  margin: 0px;
}

body a {
  color: #4183c4;
}

.container {
  margin-top: 50px;
  min-width: 300px;
  width: 50%;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

footer {
  color: rgba(236, 222, 209, 1);
  text-align: center;
  background: #eee;
  position: fixed;
  width: 100%;
  bottom: 0px;
  padding: 10px 0px;
}

.sentences {
  color: #7f8c8d;
  font-size: 0.8em;
  padding: 0px 20px;
  background: red;
  min-width: 300px;
  background: #eee;
  display: table;
  border-radius: 5px;
  margin-bottom: 60px;
}

.anijs-reserved-word {
  font-size: 1em;
  font-weight: bolder;
}

.anijs-if {
  color: #d14;
}

.anijs-on {
  color: #008080;
}

.anijs-do {
  color: #d56100;
}

.anijs-to {
  color: #4188d2;
}

.margin-right {
  margin-right: 50px;
}

.margin-left {
  margin-left: 50px;
}

.sticky {
  background: #f6f6f6;
  color: #666;
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 0px 15px;
  font-size: 80%;
  font-style: italic;
  border-radius: 5px;
}

.sticky.left {
  top: 20px;
  left: 0px;
  right: inherit;
  padding: 20px;
  width: 200px;
  font-size: 0.7em;
  border-radius: 0px 10px 10px 0px;
}

.sticky.left h4 {
  font-size: 150%;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0.33em;
  text-align: left;
}

.sticky h4 {
  text-align: center;
}

.sticky ul {
  list-style-type: disc;
  color: #4183c4;
  font-size: 90%;
}

.sticky ul li {
  margin-bottom: 10px;
}

.discover {
  margin-left: -5%;
  color: #7f8c8d;
  font-size: 80%;
}

.discover img {
  width: 16px;
  height: 16px;
}

.pure-button {
  display: inline-block;
  zoom: 1;
  line-height: normal;
  white-space: nowrap;
  vertical-align: baseline;
  text-align: center;
  cursor: pointer;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 100%;
  padding: 0.5em 1.5em 0.5em;
  color: #444;
  color: rgba(0, 0, 0, 0.8);
  border: 1px solid #999;
  border: none rgba(0, 0, 0, 0);
  background-color: #e6e6e6;
  text-decoration: none;
  border-radius: 2px;
  -webkit-transition: 0.1s linear -webkit-box-shadow;
  -moz-transition: 0.1s linear -moz-box-shadow;
  -ms-transition: 0.1s linear box-shadow;
  -o-transition: 0.1s linear box-shadow;
  transition: 0.1s linear box-shadow;
  padding: 0.4em 1em;
  border-radius: 5px;
  font-size: 80%;
}

.pure-button.primary {
  background-color: #1f8dd6;
  color: white;
}

.pure-button.secondary {
  background: rgb(66, 184, 221);
  color: white;
}
